import { useEffect, useState } from 'react';
import { Link } from 'react-router-dom';

import { API_PAYMENT } from '../../config';

import axios from '../../utils/axios';
import useLocales from '../../hooks/useLocales';

import Page from '../../components/Page';
import PageBanner from '../../components/wrappers/PageBanner';

export default function Payment() {
    const { t } = useLocales();
    const [transactions, setTransactions] = useState([]);

    useEffect(() => {
        axios.get(API_PAYMENT.getUserTrancations).then((res) => {
            if (res.status === 200) setTransactions(res.data.data);
        });
    }, []);

    return (
        <Page title="Notification">
            <PageBanner>
                <div className="container mb-8 max-w-2xl p-4">
                    <div className="flex gap-2 justify-start text-xl mb-8">
                        <Link to="/profile">
                            <span className="text-primary">{t('title.menu.profile')}</span>
                        </Link>
                        <label>/</label>
                        <label>{t('title.payments')}</label>
                    </div>
                    <div className="mb-8">
                        <label className="text-4xl">{t('title.payments')}</label>
                    </div>

                    <div className="flex flex-col py-4 gap-3">
                        {transactions.map((item, index) => (
                            <div className="alert shadow-lg" key={index}>
                                <div className="flex items-center justify-between w-full">
                                    <p className="capitalize">{item.type}</p>
                                    <p
                                        className={`font-bold text-lg text-${
                                            item.type === 'deposit' ? 'success' : 'error'
                                        }`}
                                    >
                                        {item.type === 'deposit' ? '+' : '-'}
                                        {item.amount}
                                    </p>
                                </div>
                            </div>
                        ))}
                    </div>
                    {/* <div className="flex justify-center">
                        <p className="text-primary capitalize cursor-pointer">{t('action.show_more')}</p>
                    </div> */}
                </div>
            </PageBanner>
        </Page>
    );
}
